import axios from "axios";
export const getLastestVersion = async () => {
    const res = await axios.get('https://ddragon.leagueoflegends.com/api/versions.json');
    return res.data[0];
};
export const getChampions = async (version) => {
    const res = await axios.get(`http://ddragon.leagueoflegends.com/cdn/${version}/data/en_US/champion.json`);
    let data = Object.keys(res.data.data).map((i) => {
        let championData = res.data.data[i];
        return championData;
    });
    return data;
};
